/* GLOBALRESETS */
:root{ 
  box-sizing:border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

*::selection {
  background-color: #44bcc2;
  color: white;
}

html, body, * {
  margin: 0;
  padding: 0;  
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-size: 1rem;
  background-color: #ffffff;
  background-image: url("https://www.transparenttextures.com/patterns/grunge-wall.png");
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

textarea {
  font-family: inherit;
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

a {
  text-decoration: none
}

button {
  outline: none;
}

@import url('https://fonts.googleapis.com/css2?family=Mansalva&display=swap');

/* GLOBALFONT */
:root {
  font-size: 1em; /* computed value of 16px */
  /* react-bootstrap library already uses this config, but I put it here for ilustration*/
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

  /* UNITS FOR SCALE (FONTS) */

  --rel-font-tiny: 0.625rem;
  --rel-font-small: 0.875rem;
  --rel-font-normal: 1rem; 
  --rel-font-medium: 1.125rem;
  --rel-font-big: 1.25rem;
  --rel-font-bigger: 1.5rem;
  --rel-font-large: 2rem;
  --rel-font-larger: 3rem;  
}

