/* GLOBALSPACINGSCALE */

:root {
  /* UNITS FOR SCALE (SPACING AND SIZING)*/
  --rel-space-1: 0.25em;
  --rel-space-2: 0.5em;
  --rel-space-3: 0.75em;
  --rel-space-4: 1em;
  --rel-space-5: 1.5em;
  --rel-space-6: 2em;
  --rel-space-7: 3em;
  --rel-space-8: 4em;
  --rel-space-9: 6em;
  --rel-space-10: 8em;
  --rel-space-11: 12em;
  --rel-space-12: 16em;
  --rel-space-13: 24em;
  --rel-space-14: 32em;
  --rel-space-15: 40em;
  --rel-space-16: 48em; 

  /* 
    I recommend using absolute units for
    components or elements that NOT change its size
    (ex. buttons, icons, borders). All OTHER elements 
    and spacing should use rel units.

    One exception is for font-size in nested HTML structures, 
    use rem units in that case.

    The equivalent in absolute units for the relative units shown above are: 
  */

  --abs-space-1: 4px;
  --abs-space-2: 8px;
  --abs-space-3: 12px;
  --abs-space-4: 16px;
  --abs-space-5: 24px;
  --abs-space-6: 32px;
  --abs-space-7: 48px;
  --abs-space-8: 64px;
  --abs-space-9: 96px;
  --abs-space-10: 128px;
  --abs-space-11: 192px;
  --abs-space-12: 256px;
  --abs-space-13: 384px;
  --abs-space-14: 512px;
  --abs-space-15: 640px;
  --abs-space-16: 768px;
 
}
